<template>
  <div>
    <div class="container">
      
    <ul class="collection with-header">
        <li class="collection-header "><h4 >Classes</h4></li>
    <dispclasses
      v-for="item in posts"
      v-bind:classname="item"
      v-bind:key="item.id">
    </dispclasses>
     </ul>
    </div>

    <div id="modal1" class="modal">
      
    <div class="modal-content">
      <h4>Add New Class</h4>
      <p>enter the new class name</p>
      <input v-model="message" placeholder="classname">
      <p v-if="message.length > 3"> Classes can only be three letters long</p>
    </div>
    <div class="modal-footer">
      <a  class="waves-effect waves-green btn-flat" v-on:click="addclass">Send</a>
    </div>
    
  </div>
  




    
    <div class="fixed-action-btn">
  <a class="btn-floating btn-large red">
    <i class="large material-icons  modal-trigger" data-target="modal1">add</i>
  </a>
  
</div>
    
  </div>


  
</template>












<script>
import axios from 'axios';
import dispclasses from './dispclasses'
import materialize from 'materialize-css'

export default {
   components:{
        dispclasses,
    },
  data() {
    return {
      posts: [],
      errors: [],
      message: '',
    }
  },


  // Fetches posts when the component is created.
  created() {
    axios.get('/viewclass')
    .then(response => {
      // JSON responses are automatically parsed.
      this.posts = response.data
      console.log(this.posts)
    })
    .catch(e => {
      this.errors.push(e)
    })
    document.addEventListener('DOMContentLoaded', function() {
    var elems = document.querySelectorAll('.fixed-action-btn');
    var instances = M.FloatingActionButton.init(elems);
    var telems = document.querySelectorAll('.modal');
    var tinstances = M.Modal.init(telems);
    
  });

   
    
  

  },
  methods:{
    addclass: function(event){
      if (this.message.length > 0 && this.message.length <=3){
       axios.post('/addclass',{
         name: this.message
       })
    .then(response => {
      // JSON responses are automatically parsed.
      this.newclasses = response.data
      var telems = document.querySelectorAll('.modal');
      var tinstances = M.Modal.init(telems);
      var instance = this.M.Modal.getInstance(telems);
      console.log(this.instance)
      this.instance.close()
    })
    .catch(e => {
      this.errors.push(e)
    })

    }
    },
  
  }
}

 
</script>

