<template>
    <div class="container">
        <div class="collection">
            <div class="collection">
        <h1 class="center-align">{{ $route.params.className }}</h1>
            </div>
        <table class="responsive-table">
        <thead>
         
        </thead>

        <tbody>
          <tr>
              <td><b>Sunday</b></td>
            <tableitem  v-for="item in times.sunday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
          <tr>
              <td><b>Monday</b></td>
            <tableitem v-for="item in times.monday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
            
          </tr>
          <tr>
            <td><b>Tuesday</b></td>
            <tableitem v-for="item in times.tuesday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
          <tr>
              <td><b>Wednesday</b></td>
            <tableitem v-for="item in times.wednesday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
          <tr>
              <td><b>Thursday</b></td>
            <tableitem v-for="item in times.thursday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
          <tr>
              <td><b>Friday</b></td>
            <tableitem v-for="item in times.friday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
          <tr>
              <td><b>Saturday</b></td>
            <tableitem v-for="item in times.saturday"
                    v-bind:subject="item"
                    v-bind:key="item.id">
            </tableitem>
          </tr>
        </tbody>
      </table>
        </div>
        <router-link to="/" class="waves-effect waves-light btn">Back</router-link>
    
    
    
    <div id="modal1" class="modal">
      
    <div class="modal-content">
      <h4>Modify Timetable</h4>
      <p>enter the subject name</p>
      <div class="input-field col s12">
    <select v-model="subject">
      <option value="" disabled selected>Choose your option</option>
      <option value="1">Option 1</option>
      <option value="2">Option 2</option>
      <option value="3">Option 3</option>
    </select>
    
  </div>
    </div>
    <div class="modal-footer">
      <a  class="waves-effect waves-green btn-flat" >Send</a>
    </div>
    
  </div>
  




    
    <div class="fixed-action-btn">
  <a class="btn-floating btn-large red">
    <i class="large material-icons  modal-trigger" data-target="modal1">add</i>
  </a>
  
</div>
    
    
    
    </div>
</template>

<script>
import axios from 'axios';
import tableitem from './tableitem'
import materialize from 'materialize-css'


export default {
   
  name: 'times',
  components:{
      tableitem,
  },
  data () {
      return {
          classtimes:[],
          times:[],
          errors:[],
          subject:'',
          day:'',
          timetype:''
      }
  },
  created() {
    axios.post('/ClassTimes',{
        class: this.$route.params.className
    })
    .then(response => {
      // JSON responses are automatically parsed.
      this.classtimes = response.data;
      this.times = this.classtimes.classTimes

      console.log(this.classtimes)
    })
    .catch(e => {
      console.log(e)
    })

     document.addEventListener('DOMContentLoaded', function() {
    var elems = document.querySelectorAll('.fixed-action-btn');
    var instances = M.FloatingActionButton.init(elems);
    
  });

    document.addEventListener('DOMContentLoaded', function() {
    var newelems = document.querySelectorAll('.modal');
    var newinstances = M.Modal.init(newelems);
  });

  }


  
}
</script>



